import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class OrthodictService {
    Json_URL = './assets/words.json';

  constructor(private http: HttpClient) {}

  getDict() {
    return this.http.get<Words_in_Json>(this.Json_URL);
    }
}

export interface Words_in_Json {
    class_1: Array<string>;
    class_2: Array<string>;
    class_3: Array<string>;
    class_4: Array<string>;
}